import {
  Box,
  Fab,
  FormControl,
  Grid,
  Hidden,
  IconButton,
  InputLabel,
  MenuItem,
  Paper,
  Select,
  TextField,
  Typography,
} from "@material-ui/core";
import { Add, CastForEducation, Close, SkipPrevious } from "@material-ui/icons";
import { Rating } from "@material-ui/lab";
import React, { useContext, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  addexperimentalSkill,
  changeexperienceDescription,
  changeexperienceSkillLevel,
  changeexperienceSkillTitle,
  removeexperimentalSkill,
} from "../action/experimentalSkills";
import {
  addHonor,
  changehonorMonth,
  changehonorTitle,
  changehonorYear,
  removeHonor,
} from "../action/honors";
import {
  addLanguage,
  changehearSkill,
  changelanguage,
  changereadSkill,
  changespeakSkill,
  changewriteSkill,
  removeLanguage,
} from "../action/language";
import ResumeContext from "../context/resumeContext";
import {
  saveLanguageSkills,
  saveHonores,
  saveExperimentSkills,
} from "../services/resumeService";
import { useStyles } from "../utils/styles";

const Skill = () => {
  const months = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];

  const context = useContext(ResumeContext);

  const dispatch = useDispatch();

  const languages = useSelector((state) => state.languages);
  const experimentalSkills = useSelector((state) => state.experimentalSkills);
  const honors = useSelector((state) => state.honors);

  const handleSaveSkills = async (event) => {
    event.preventDefault();

    const response = await saveLanguageSkills(languages);
    const response2 = await saveExperimentSkills(experimentalSkills);
    const response3 = await saveHonores(honors);
    if (
      response.status < 210 &&
      response2.status < 210 &&
      response3.status < 210
    ) {
      context.handleNext();
      context.initializeData();
    }
  };

  const classes = useStyles();
  return (
    <form onSubmit={handleSaveSkills}>
      <Typography variant="h5" style={{ marginTop: 20 }} gutterBottom>
        
      </Typography>
      {languages.map((language, index) => (
        <Paper
          style={{
            padding: "25px 30px",
            marginBottom: 15,
            position: "relative",
          }}
        >
          {languages.length > 1 ? (
            <IconButton
              color="secondary"
              onClick={() => dispatch(removeLanguage(index))}
              size="small"
              style={{ position: "absolute", top: 5, left: 5 }}
            >
              <Close />
            </IconButton>
          ) : null}
          <Grid container justify="center" spacing={2}>
            <Grid md={4} item>
              <TextField
                className={classes.formControl}
                label=" "
                required
                name="language"
                key={Math.random()}
                defaultValue={language.Name}
                onBlur={(e) => dispatch(changelanguage(e, index))}
              />
            </Grid>
            <Grid md={2} item>
              <Box component="fieldset" borderColor="transparent">
                <Typography component="legend"></Typography>
                <Rating
                  size="small"
                  required
                  // name="readSkill"
                  value={language.readSkill}
                  onChange={(e) => dispatch(changereadSkill(e, index))}
                />
              </Box>
            </Grid>
            <Grid md={2} item>
              <Box component="fieldset" borderColor="transparent">
                <Typography component="legend"></Typography>
                <Rating
                  size="small"
                  required
                  // name="writeSkill"
                  value={language.writeSkill}
                  onChange={(e) => dispatch(changewriteSkill(e, index))}
                />
              </Box>
            </Grid>
            <Grid md={2} item>
              <Box component="fieldset" borderColor="transparent">
                <Typography component="legend"></Typography>
                <Rating
                  size="small"
                  required
                  // name="hearSkill"
                  value={language.hearSkill}
                  onChange={(e) => dispatch(changehearSkill(e, index))}
                />
              </Box>
            </Grid>
            <Grid md={2} item>
              <Box component="fieldset" borderColor="transparent">
                <Typography component="legend"></Typography>
                <Rating
                  size="small"
                  required
                  // name="speakSkill"
                  value={language.speakSkill}
                  onChange={(e) => dispatch(changespeakSkill(e, index))}
                />
              </Box>
            </Grid>
          </Grid>
        </Paper>
      ))}
      <Grid container spacing={1}>
        <Fab
          color="primary"
          size="small"
          style={{ margin: "auto" }}
          onClick={() => {
            dispatch(addLanguage());
          }}
        >
          <Add />
        </Fab>
      </Grid>
      <Typography variant="h5" style={{ marginTop: 20 }} gutterBottom>
          
      </Typography>
      <Grid container spacing={1}>
        {experimentalSkills.map((experimentalSkill, index) => (
          <Grid item spacing={3} xs={12} sm={6}>
            <Paper
              style={{
                padding: "25px 30px",
                marginBottom: 15,
                position: "relative",
              }}
            >
              {experimentalSkills.length > 1 ? (
                <IconButton
                  color="secondary"
                  onClick={() => dispatch(removeexperimentalSkill(index))}
                  size="small"
                  style={{ position: "absolute", top: 5, left: 5 }}
                >
                  <Close />
                </IconButton>
              ) : null}
              <Grid container justify="center" spacing={2}>
                <Grid xs={6} item>
                  <TextField
                    label=" "
                    name="experienceSkillTitle"
                    key={Math.random()}
                    defaultValue={experimentalSkill.Name}
                    onBlur={(e) =>
                      dispatch(changeexperienceSkillTitle(e, index))
                    }
                  />
                </Grid>
                <Grid xs={6} item>
                  <Box component="fieldset" borderColor="transparent">
                    <Typography component="legend"></Typography>
                    <Rating
                      size="small"
                      // name="experienceSkillLevel"
                      value={experimentalSkill.skillLevel}
                      onChange={(e) =>
                        dispatch(changeexperienceSkillLevel(e, index))
                      }
                    />
                  </Box>
                </Grid>
                <Grid xs={12} item>
                  <TextField
                    className={classes.formControl}
                    name="experienceDescription"
                    key={Math.random()}
                    defaultValue={experimentalSkill.description}
                    onBlur={(e) =>
                      dispatch(changeexperienceDescription(e, index))
                    }
                    multiline
                    label=""
                  />
                </Grid>
              </Grid>
            </Paper>
          </Grid>
        ))}
      </Grid>
      <Grid container spacing={1}>
        <Fab
          color="primary"
          size="small"
          style={{ margin: "auto" }}
          onClick={() => {
            dispatch(addexperimentalSkill());
          }}
        >
          <Add />
        </Fab>
      </Grid>
      {/* <Typography variant="h5" style={{ marginTop: 20 }} gutterBottom>
           
      </Typography>
      <Paper style={{ padding: "25px 30px" }}>
        <Grid container justify="center" alignItems="center" spacing={2}>
          <Grid xs={4} md={3} item>
            <FormControl className={classes.formControl}>
              <InputLabel> </InputLabel>
              <Select required name="certificateType"></Select>
            </FormControl>
          </Grid>
          <Grid xs={8} md={3} item>
            <TextField
              label=""
             required name="certificateTitle"
              className={classes.formControl}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <div class="file-drop-area">
              <span class="fake-btn">
                <CastForEducation />
              </span>
              <span class="file-msg">{evidence}</span>
              <input
                class="file-input"
                type="file"
               required name="evidence"
                onChange={onChangeEvidence}
              />
            </div>
          </Grid>
        </Grid>
      </Paper> */}
      <Typography variant="h5" style={{ marginTop: 20 }} gutterBottom>
        
      </Typography>
      {honors.map((honor, index) => (
        <Paper
          style={{
            padding: "25px 30px",
            marginBottom: 15,
            position: "relative",
          }}
        >
          {honors.length > 1 ? (
            <IconButton
              color="secondary"
              onClick={() => dispatch(removeHonor(index))}
              size="small"
              style={{ position: "absolute", top: 5, left: 5 }}
            >
              <Close />
            </IconButton>
          ) : null}
          <Grid container justify="center" spacing={2}>
            <Grid xs={12} md={8} item>
              <TextField
                name="honorTitle"
                key={Math.random()}
                defaultValue={honor.honorTitle}
                onBlur={(e) => dispatch(changehonorTitle(e, index))}
                label=""
                className={classes.formControl}
                placeholder="  :         "
              />
            </Grid>
            <Grid xs={12} md={4} item>
              <InputLabel></InputLabel>
              <Grid container spacing={1}>
                <Grid item xs={6}>
                  <Select
                    name="honorMonth"
                    key={Math.random()}
                    defaultValue={honor.honorMonth}
                    onChange={(e) => dispatch(changehonorMonth(e, index))}
                    className={classes.formControl}
                  >
                    <MenuItem disabled value="def">
                      
                    </MenuItem>
                    {months.map((v, k) => (
                      <MenuItem value={k + 1}>{v}</MenuItem>
                    ))}
                  </Select>
                </Grid>
                <Grid item xs={6}>
                  <TextField
                    name="honorYear"
                    key={Math.random()}
                    defaultValue={honor.honorYear}
                    onBlur={(e) => dispatch(changehonorYear(e, index))}
                    placeholder=""
                    type="number"
                    className={classes.formControl}
                  />
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      ))}
      <Grid container spacing={1}>
        <Fab
          color="primary"
          size="small"
          style={{ margin: "auto" }}
          onClick={() => {
            dispatch(addHonor());
          }}
        >
          <Add />
        </Fab>
      </Grid>
      <Fab
        style={{
          position: "fixed",
          bottom: 50,
          left: 50,
        }}
        variant="contained"
        color="primary"
        type="submit"
        // onClick={}
        size="medium"
      >
        <Hidden xsDown>  </Hidden>
        <SkipPrevious className={classes.extendedIcon} />
      </Fab>
    </form>
  );
};

export default Skill;
