import {
  Checkbox,
  FormControl,
  FormControlLabel,
  Grid,
  IconButton,
  InputLabel,
  MenuItem,
  Paper,
  Select,
  TextField,
  Tooltip,
  Typography,
} from "@material-ui/core";
import { Close, Help } from "@material-ui/icons";
import { Autocomplete } from "@material-ui/lab";
import React, { Fragment } from "react";
import { useStyles } from "../../utils/styles";
import provinces from "../../utils/provinces.json";
import cities from "../../utils/cities.json";
import {
  changecooperateType,
  changeendJobMonth,
  changeendJobYear,
  changeincome,
  changejobCenter,
  changejobCity,
  changejobCountry,
  changejobDescription,
  changejobGroup,
  changejobProvince,
  changejobTitle,
  changenumber,
  changeseniorLevel,
  changestartJobMonth,
  changestartJobYear,
  changestillWorking,
  changetitleCenter,
  removeJob,
} from "../../action/jobs";
import { useDispatch } from "react-redux";

const Job = ({ job, index, length }) => {
  const jobGroups = ["", "", " ", ""];
  const cooperateTypes = [
    "  ",
    "  ",
    "  ",
    "",
    " ",
  ];
  const months = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const seniorLevels = [" ", "", ""];

  const dispatch = useDispatch();

  const citiesFiltered = [
    ...cities.filter((city) => city.province == job.jobProvince),
  ];

  const classes = useStyles();

  return (
    <Paper
      style={{ padding: "25px 30px", marginBottom: 15, position: "relative" }}
    >
      {length > 1 ? (
        <IconButton
          color="secondary"
          onClick={() => dispatch(removeJob(index))}
          size="small"
          style={{ position: "absolute", top: 5, left: 5 }}
        >
          <Close />
        </IconButton>
      ) : null}
      <Grid container justify="center" spacing={2}>
        <Grid item xs={6} sm={3}>
          <Tooltip title=" ">
            <TextField
              className={classes.formControl}
              onBlur={(e) => dispatch(changejobTitle(e, index))}
              name="jobTitle"
              required
              key={Math.random()}
              defaultValue={job.jobTitle}
              label={
                <Fragment>
                   {" "}
                  <Help
                    style={{ fontSize: 14, transform: "rotateY(180deg)" }}
                  />
                </Fragment>
              }
            />
          </Tooltip>
        </Grid>
        <Grid item xs={6} sm={3}>
          <FormControl className={classes.formControl}>
            <InputLabel> </InputLabel>
            <Select
              onChange={(e) => dispatch(changejobGroup(e, index))}
              name="jobGroup"
              required
              value={job.jobGroup}
            >
              {jobGroups.map((group) => (
                <MenuItem value={group}>{group}</MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={3}>
          <TextField
            className={classes.formControl}
            onBlur={(e) => dispatch(changejobCenter(e, index))}
            name="jobCenter"
            required
            key={Math.random()}
            defaultValue={job.jobCenter}
            label=" "
          />
        </Grid>
        <Grid item xs={6} sm={3}>
          <TextField
            className={classes.formControl}
            onBlur={(e) => dispatch(changetitleCenter(e, index))}
            name="titleCenter"
            required
            key={Math.random()}
            defaultValue={job.titleCenter}
            label=" "
          />
        </Grid>
      </Grid>
      <Grid container justify="center" style={{ marginTop: 20 }} spacing={2}>
        <Grid item xs={6} sm={2}>
          <FormControl className={classes.formControl}>
            <InputLabel> </InputLabel>
            <Select
              onChange={(e) => dispatch(changecooperateType(e, index))}
              name="cooperateType"
              required
              value={job.cooperateType}
            >
              {cooperateTypes.map((group) => (
                <MenuItem value={group}>{group}</MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={2}>
          <FormControl className={classes.formControl}>
            <InputLabel> </InputLabel>
            <Select
              onChange={(e) => dispatch(changeseniorLevel(e, index))}
              name="seniorLevel"
              required
              value={job.seniorLevel}
            >
              {seniorLevels.map((group) => (
                <MenuItem value={group}>{group}</MenuItem>
              ))}
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={2}>
          <FormControl className={classes.formControl}>
            <InputLabel></InputLabel>
            <Select
              onChange={(e) => dispatch(changejobCountry(e, index))}
              name="jobCountry"
              value={job.jobCountry}
              required
            >
              <MenuItem value={""}></MenuItem>
            </Select>
          </FormControl>
        </Grid>
        <Grid item xs={6} sm={3}>
          <Autocomplete
            options={provinces}
            getOptionLabel={(option) => option.title}
            className={classes.formControl}
            value={{ title: job.jobProvince }}
            onChange={(e, newValue) =>
              newValue
                ? dispatch(changejobProvince(newValue.title, index))
                : dispatch(changejobProvince("", index))
            }
            required
            renderInput={(params) => (
              <TextField name {...params} name="jobProvince" label="" />
            )}
          />
        </Grid>
        <Grid item xs={6} sm={3}>
          <Autocomplete
            options={citiesFiltered}
            getOptionLabel={(option) => option.city}
            label=""
            value={{ city: job.jobCity }}
            className={classes.formControl}
            onChange={(e, newValue) =>
              newValue
                ? dispatch(changejobCity(newValue.city, index))
                : dispatch(changejobCity("", index))
            }
            required
            renderInput={(params) => (
              <TextField {...params} name="jobCity" label="" />
            )}
          />
        </Grid>
      </Grid>
      <Grid
        container
        alignItems="flex-end"
        spacing={2}
        style={{ marginTop: 20 }}
      >
        <Grid item xs={6} sm={4}>
          <InputLabel></InputLabel>
          <Grid container spacing={1}>
            <Grid item xs={6} sm={7}>
              <Select
                onChange={(e) => dispatch(changestartJobMonth(e, index))}
                name="startJobMonth"
                value={job.startJobMonth}
                required
                className={classes.formControl}
              >
                <MenuItem disabled value="def">
                  
                </MenuItem>
                {months.map((v, k) => (
                  <MenuItem value={k + 1}>{v}</MenuItem>
                ))}
              </Select>
            </Grid>
            <Grid item xs={6} sm={5}>
              <TextField
                className={classes.formControl}
                onBlur={(e) => dispatch(changestartJobYear(e, index))}
                name="startJobYear"
                required
                key={Math.random()}
                defaultValue={job.startJobYear}
                type="number"
                placeholder=""
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={6} sm={4}>
          <InputLabel></InputLabel>
          <Grid container spacing={1}>
            <Grid item xs={6} sm={7}>
              <Select
                onChange={(e) => dispatch(changeendJobMonth(e, index))}
                name="endJobMonth"
                required={!Boolean(job.stillWorking)}
                value={job.endJobMonth}
                className={classes.formControl}
                disabled={job.stillWorking}
              >
                <MenuItem disabled value="def">
                  
                </MenuItem>
                {months.map((v, k) => (
                  <MenuItem value={k + 1}>{v}</MenuItem>
                ))}
              </Select>
            </Grid>
            <Grid item xs={6} sm={5}>
              <TextField
                className={classes.formControl}
                onBlur={(e) => dispatch(changeendJobYear(e, index))}
                name="endJobYear"
                required={!Boolean(job.stillWorking)}
                key={Math.random()}
                defaultValue={job.endJobYear}
                type="number"
                disabled={job.stillWorking}
                placeholder=""
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={6} sm={4}>
          <FormControlLabel
            control={
              <Checkbox
                checked={job.stillWorking}
                onChange={(e) => dispatch(changestillWorking(e, index))}
              />
            }
            label="  "
          />
        </Grid>
      </Grid>
      <Grid
        container
        alignItems="flex-end"
        spacing={2}
        style={{ marginTop: 20 }}
      >
        <Grid item xs={6} sm={4}>
          <TextField
            label=" "
            onBlur={(e) => dispatch(changeincome(e, index))}
            name="income"
            required
            key={Math.random()}
            defaultValue={job.income}
            type="number"
            placeholder="4000000 "
            className={classes.formControl}
          />
        </Grid>
        <Grid item xs={6} sm={4}>
          <TextField
            label=" "
            type="number"
            placeholder="88888888"
            onBlur={(e) => dispatch(changenumber(e, index))}
            name="phoneNumber"
            required
            InputProps={{ inputProps: { min: 10000000, max: 99999999 } }}
            key={Math.random()}
            defaultValue={job.number}
            className={classes.formControl}
          />
        </Grid>
      </Grid>
      <Typography
        style={{ marginTop: 20 }}
        variant="body1"
        display="block"
        gutterBottom
      >
         /  ()
      </Typography>
      <Typography variant="caption" display="block" gutterBottom>
                      
            .
      </Typography>
      <Typography variant="caption" display="block" gutterBottom>
            Enter  .
      </Typography>
      <Grid item style={{ margin: "10px" }} justify="center" spacing={2}>
        <TextField
          className={classes.formControl}
          onBlur={(e) => dispatch(changejobDescription(e, index))}
          name="jobDescription"
          key={Math.random()}
          defaultValue={job.jobDescription}
          multiline
          label="  "
        />
      </Grid>
    </Paper>
  );
};

export default Job;
